import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
// 定义状态属性
let currentIndex = 0
let loginname = ''
let userId = 0
let sss = 0
let cart = []
let buyData = []
try {
  if (localStorage.nbIndex) {
    currentIndex = localStorage.nbIndex
  }
} catch (e) {

}
try {
  if (localStorage.userName) {
    loginname = localStorage.userName
  }
} catch (e) {

}
try {
  if (localStorage.sum) {
    sss = localStorage.sum
  }
} catch (e) {

}
try {
  if (localStorage.id) {
    userId = localStorage.id
  }
} catch (e) {

}
try {
  if (JSON.parse(window.localStorage.getItem('key'))) {
    cart = JSON.parse(window.localStorage.getItem('key'))
  }
} catch (e) {

}
try {
  if (JSON.parse(window.localStorage.getItem('buy'))) {
    buyData = JSON.parse(window.localStorage.getItem('buy'))
  }
} catch (e) {

}
var state = {
  shownav: true,
  cartDatas: cart, // 购物车中数据
  checkAllState: false,
  userName: loginname,
  nbIndex: currentIndex,
  id: userId,
  buyDatas: buyData,
  sum: sss
}
// store中的计算属性
var getters = {
  count (state) {
    return state.count
  },
  isEvenOrOdd () {
    return state.count % 2 === 0 ? '偶数' : '奇数'
  },
  shownav (state) { //  计算属性   底部导航显示隐藏
    return state.shownav
  },
  cartDatas (state) { //  购物车的数据
    return state.cartDatas
  },
  checkAllState (state) { // 全选的状态
    return state.checkAllState
  }
}

//  管理mutations
var actions = {
  add ({
    commit
  }) { // 添加
    commit('add')
  },
  decrement ({
    commit,
    state
  }) { // 减少
    if (state.count > 3) {
      commit('decrement')
    }
  },
  hideNav ({
    commit
  }) { // 底部导航隐藏
    commit('HIDENAV')
  },
  showNav ({
    commit
  }) { // 底部导航显示
    commit('SHOWNAV')
  },
  addCart ({
    commit
  }, data) { // 购物车的数据
    commit('ADDCART', data)
  },
  delCartData ({
    commit,
    state
  }, data) { // 删除购物车数据
    commit('DELCARTDATA', data)
  }
}
//  改变状态   commit()
var mutations = {
  add (state, data) { //  添加
    state.count++
  },
  decrement (state, data) { //  减少
    state.count--
  },
  HIDENAV (state) { // 底部导航隐藏
    state.shownav = false
  },
  SHOWNAV (state) { //  底部导航显示
    state.shownav = true
  },
  ADDCART (state, data) { //  购物车的数据
    state.cartDatas = data
    let obj = JSON.stringify(state.cartDatas)
    localStorage.setItem('key', obj)
  },
  DELCARTDATA (state, data) { // 删除数据
    for (let i = 0; i < state.cartDatas.length; i++) {
      if (state.cartDatas[i].product_id === data) {
        state.cartDatas.splice(i, 1)
        break
      }
    }
  },
  CHANGENBINDEX (state, nbIndex) {
    state.nbIndex = nbIndex
    localStorage.nbIndex = nbIndex
  },
  LOGINNAME (state, datas) {
    state.userName = datas.userName
    state.id = datas.id
    localStorage.userName = datas.userName
    localStorage.id = datas.id
  },
  BUY (state, datas) {
    state.buyDatas = datas
    let obj = JSON.stringify(state.buyDatas)
    localStorage.setItem('buy', obj)
  },
  SUM (state, datas) {
    state.sum = datas
    localStorage.sum = datas
  }
}

// 创建store对象
const store = new Vuex.Store({
  state,
  getters,
  actions,
  mutations
})
// 导出
export default store



