<template>
	<div id="caregoty_head">
		<header class="top_bar">
	        <a @click="indexChange" class="icon_back"></a>
	        <form action="" class="goods_search">
	            <input type="search" class="goods_search_content" placeholder="搜索"  @click="goSearch($event)">
	        </form>
	        <a href="#" class="icon_menu"></a>
	    </header>
	</div>
</template>
<script>
import { mapGetters, mapActions, mapState } from "vuex";
export default {
  methods: {
    indexChange() {
      this.$store.commit("CHANGENBINDEX", 0);
      this.$router.push("/home");
    },
    goSearch(event) {
      this.$router.push("/search");
    }
  },
  computed: {
    ...mapState(["nbIndex"])
  }
};
</script>



