<template>
  <div>
    <div class="cart_content clearfix" v-for="(item,index) in cartDatas" :class="{'special':length==(index+1)&&length>=4}">
            <div class="cart_shop clearfix">
                <div class="shop_info clearfix">
                    <input type ="checkbox" v-model="item[0].check" @change="curCheckChange(item[0].product_id)"/>
                    <span class="shop_name">{{item[0].shop_name}}</span>
                </div>
                <div class="cart_free clearfix">
                    <span class="free_tip">优惠券></span>
                </div>
            </div>
            <div class="cart_item">
                <div class="cart_detial_box clearfix">
                    <a href="#" class="cart_product_link">
                        <img v-lazy="item[0].product_img_url" alt="">
                    </a>
                    <div class="item_names">
                        <a href="#">
                            <span>{{item[0].product_name}}</span>
                        </a>
                    </div>
                    <div class="cart_weight">
                        <i class="my_weigth">重量:0.45kg</i>
                        <span class="my_color">颜色:AT800/16</span>
                    </div>
                    <div class="cart_product_sell">
                        <span class="product_money">￥<strong class="real_money">{{item[0].product_uprice}}</strong></span>
                        <div class="cart_add clearfix">
                            <span class="my_add" @click="incrementData(item[0].product_id)">+</span>
                            <input type="tel" class="my_count" v-model="item[0].num" readonly="readonly">
                            <span class="my_jian" @click="decrementData(item[0].product_id)">-</span>
                        </div>
                    </div>
                    <div class="cart_del clearfix" @click="delCart(item[0].order_id)">
                        <div class="del_top" >
                        </div>
                        <div class="del_bottom">
                        </div>
                    </div>
                </div>
            </div>

        </div>
        <div class="pop" v-show="popStatus">
          <div class="pop_box">
              <div class="del_info">
                  确定要删除该商品吗？
              </div>
              <div class="del_cancel" @click="delCancel">
                  取消
              </div>
              <div class="del_ok" @click="delOk">
                  确定
              </div>
          </div>
        </div>
        <div class="cart_fo">
		<footer class="cart_footer">
		    <div class="all_check_box">
		        <!--<div class="check_box"></div>-->
				<input type ="checkbox" v-model="checkAllState" @change="CAC()"/>
		        <span>全选</span>
		    </div>
		    <div class="count_money_box">
		        <div class="heji">
		            <strong>合计:</strong>
		            <strong>￥</strong>
		            <strong>{{this.$store.state.sum}}</strong>
		        </div>
		        <div class="total_money clearfix">
		            <span>总额:</span>
		            <i>￥</i>
		            <span>{{this.$store.state.sum}}</span>
		            <span>立减:</span>
		            <i>￥</i>
		            <span>0.00</span>
		        </div>
		            <a href="#" class="go_pay">
		                <span @click="toPay">去结算(1)</span>
		            </a>
		    </div>
		</footer>
	</div>
  </div>
</template>

<script>
//mapGetters 辅助函数  获取store中的getters值
import { mapGetters, mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      popStatus: false,
      curId: "",
      arrPro: [],
      Datas: [],
      length: 0
    };
  },
  computed: {
    ...mapState(["id", "cartDatas", "sum"]),
    checkAllState: {
      get() {
        return this.$store.state.checkAllState;
      },
      set(val) {
        this.$store.state.checkAllState = !val;
      }
    }
  },
  mounted() {
    this.$store.dispatch("hideNav"); //触发store中的隐藏的方法
    this.init(this.id);
  },
  destroyed() {
    this.$store.dispatch("showNav"); //触发store中的显示的方法
  },
  methods: {
    incrementData(id) {
      for (let index = 0; index < this.cartDatas.length; index++) {
        if (this.cartDatas[index][0].product_id === id) {
          this.cartDatas[index][0].num = this.cartDatas[index][0].num + 1;
        }
      }
      this.curCheckChange(id);
      this.curCheckChange(id);
    },
    decrementData(id) {
      for (let index = 0; index < this.cartDatas.length; index++) {
        if (this.cartDatas[index][0].product_id === id) {
          if (this.cartDatas[index][0].num == 1) {
            return;
          }
          this.cartDatas[index][0].num--;
        }
        this.curCheckChange(id);
        this.curCheckChange(id);
      }
    },
    delCart: function(id) {
      //删除
      this.popStatus = true;
      this.curId = id;
    },
    delCancel: function() {
      //取消
      this.popStatus = false;
    },
    delOk: function() {
      //确定
      this.popStatus = false;
      let self = this;
      self.$http.post("/delOrder", { params: { mId: this.curId } }).then(
        res => {
          if (res.status == "200") {
            this.$router.go(0);
          }
        },
        error => {
          console.log(error);
        }
      );
    },
    curCheckChange: function(id) {
      for (let index = 0; index < this.cartDatas.length; index++) {
        if (this.cartDatas[index][0].product_id === id) {
          this.cartDatas[index][0].check == false
            ? (this.cartDatas[index][0].check = false)
            : (this.cartDatas[index][0].check = true);
        }
      }
      var items = this.cartDatas.filter(function(item, index) {
        return item[0].check == true;
      });
      // 匹配选中状态与lists的长度
      items.length === this.cartDatas.length
        ? (this.checkAllState = false)
        : (this.checkAllState = true);
      this.total();
    },
    init(id) {
      let self = this;
      self.checkAllState = true;
      self.$http.get("/cart1", { params: { mId: id } }).then(
        res => {
          if (res.status == "200") {
            self.arrPro = res.data;
            self.arrPro.forEach(function(item, index) {
              let pid = item.product_id;
              self.$http.get("/cart2", { params: { Id: pid } }).then(
                res => {
                  if (res.status == "200") {
                    self.length++;
                    self.Datas.push(res.data);
                    self.Datas[index][0].num = self.arrPro[index].ocount;
                    self.Datas[index][0].order_id = self.arrPro[index].order_id;
                    let sid = res.data[0].shop_id;
                    self.$http.get("/cart3", { params: { Id: sid } }).then(
                      res => {
                        if (res.status == "200") {
                          self.Datas[index][0].shop_name =
                            res.data[0].shop_name;
                          self.Datas[index][0].check = false;
                        }
                      },
                      error => {
                        console.log(error);
                      }
                    );
                  }
                },
                error => {
                  console.log(error);
                }
              );
            });
          }
        },
        error => {
          console.log(error);
        }
      );
      this.Datas = JSON.parse(JSON.stringify(this.Datas));
      this.$store.commit("ADDCART", this.Datas);
      this.$store.commit("SUM", 0);
    },
    toPay() {
      var datas = [];
      for (let index = 0; index < this.cartDatas.length; index++) {
        if (this.cartDatas[index][0].check == true) {
          datas.push(this.cartDatas[index][0]);
        }
      }
      if (datas.length > 0) {
        this.$store.commit("BUY", datas);
        this.$router.push("/pay");
      } else {
        alert("请选择要购买的商品");
      }
    },
    total() {
      var sum = 0;
      for (let index = 0; index < this.cartDatas.length; index++) {
        if (this.cartDatas[index][0].check == true) {
          sum +=
            this.cartDatas[index][0].product_uprice *
            this.cartDatas[index][0].num;
        }
      }
      this.$store.commit("SUM", sum);
    },
    CAC() {
      this.checkAllState == false
        ? (this.checkAllState = false)
        : (this.checkAllState = true);
      for (let index = 0; index < this.cartDatas.length; index++) {
        this.cartDatas[index][0].check = this.checkAllState;
      }
      this.total();
    }
  }
};
</script>



