<template>
	<div class="my_l">
		<header class="top_bar">
		    <a @click="indexChange" class="icon_back"></a>
		    <h3 class="cartname">登录</h3>
		</header>
		<main class="user_login_box">
		    <div class="login_dialog">
		        <div class="_username">
		            <input type="text" name="username" placeholder="请输入用户名" class="user_input" v-model="name"/>
		        </div>
		        <div class="_username u_passwd">
		            <input type="password" name="password" placeholder="请输入密码" class="user_input" v-model="password"/>
		        </div>

		        <div class="login_box">
		            <a @click="goLogin()" class="btn_login">登录</a>
		        </div>
		        <div class="go_reg_box">
		            <router-link tag="span" to="/register">快速注册</router-link>
		        </div>
		    </div>
		</main>
	</div>
</template>
<script>
import { mapGetters, mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      name: "",
      password: ""
    };
  },
  computed: {
    ...mapState(["nbIndex"]),
    ...mapState(["userName"])
  },
  methods: {
    getInfo() {
      if (this.userName) {
        this.$router.push("/mine");
      }
    },
    indexChange() {
      this.$store.commit("CHANGENBINDEX", 0);
      this.$router.push("/home");
    },
    goLogin() {
      if (this.name == "" || this.password == "") {
        alert("请输入信息");
      } else {
        let self = this;
        let data = {
          loginName: this.name,
          loginPawd: this.password
        };
        self.$http.post("/login", data).then(
          res => {
            if (res.status == 200) {
              if (res.data.status == 1) {
                let datas = {
                  userName: res.data.user_name,
                  id: res.data.user_id
                };
                this.$store.commit("LOGINNAME", datas);
                self.$router.push("/mine");
              } else {
                alert(res.data.msg);
              }
            } else {
              alert("请求失败");
            }
          },
          error => {
            console.log(error);
          }
        );
      }
    }
  },
  mounted() {
    if (this.userName) {
      this.$router.push("/");
    }
    this.getInfo();
    this.$store.dispatch("hideNav"); //触发store中的隐藏的方法
  },
  destroyed() {
    this.$store.dispatch("showNav"); //触发store中的显示的方法
  }
};
</script>
<style lang="stylus" scoped>
.user_login_box {
  max-width: 640px;
  min-width: 300px;
  margin: 0 auto;
  height: 200px;
  padding-top: 40px;

  .login_dialog {
    width: 100%;
    height: 100%;
    padding: 10px;

    ._username {
      height: 50px;
      width: 100%;
      background-color: #fff;
      margin-top: 20px;
      border: 1px solid #bebebe;
      border-radius: 2px;

      .user_input {
        width: 100%;
        height: 100%;
        font-size: 16px;
        padding-left: 6px;
      }
    }

    .u_passwd {
      margin-top: 10px;
    }

    .login_box {
      height: 50px;
      width: 100%;
      background-color: #fff;
      border: 1px solid #F23030;
      border-radius: 4px;
      margin-top: 30px;

      a {
        display: block;
        width: 100%;
        height: 100%;
        line-height: 50px;
        text-align: center;
        color: #F23030;
      }
    }

    .go_reg_box {
      float: right;

      span {
        display: inline-block;
        color: #aaa;
        height: 20px;
        line-height: 20px;
        margin-top: 10px;
      }
    }
  }
}
</style>



