<template>
	<div class="my_l">
		<header class="top_bar">
		    <a @click="indexChange" class="icon_back"></a>
		    <h3 class="cartname">快速注册</h3>
		</header>
		<main class="user_login_box">
		    <div class="login_dialog">
		        <div class="input u_username clearfix">
                <input type="text" name="username" class="user_input" placeholder="请输入您的用户名" v-model="username" ref="input" @blur="nameCheck($event)" @focus="tipHide($event)">
                <div class="tip-error">{{this.tip1}}</div>
		        </div>
		        <div class="input u_passwd clearfix">
		            <input type="password" name="password" placeholder="请输入您的密码" class="user_input" v-model="password" ref="input" @blur="passCheck($event)" @focus="tipHide($event)"/>
                <div class="tip-error">{{this.tip2}}</div>
		        </div>
		        <div class="input u_number clearfix">
		            <input type="tel" name="password" placeholder="请输入您的手机号码" class="user_input" v-model="usernumber" ref="input" @blur="numberCheck($event)" @focus="tipHide($event)"/>
                <div class="tip-error">{{this.tip3}}</div>
		        </div>
		        <div class="login_box">
		            <a @click="goRegister()" class="btn_login">注册</a>
		        </div>
		    </div>
		</main>
	</div>
</template>
<script>
import { mapGetters, mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      username: "",
      password: "",
      usernumber: "",
      tip1: "",
      tip2: "",
      tip3: ""
    };
  },
  methods: {
    tipHide(e) {
      e.currentTarget.nextElementSibling.style.display="none";
    },
    indexChange() {
      this.$store.commit("CHANGENBINDEX", 4);
      this.$router.push("/login");
    },
    passCheck(e){
      e.currentTarget.nextElementSibling.style.display="block";
      var filter = /^[a-zA-Z]\w{5,17}$/; //正则
      if (this.password == "") {
        this.tip2 = '密码不能为空';
      } else if (!filter.test(this.password)) {
        this.tip2 = "密码格式错误";
      } else {
        this.tip2 = "";
      }
    },
    nameCheck(e) {
      e.currentTarget.nextElementSibling.style.display="block";
      var filter = /^[a-zA-Z][a-zA-Z0-9_]{4,15}$/; //正则
      if (this.username == "") {
        this.tip1 = '用户名不能为空';
      } else if (!filter.test(this.username)) {
        this.tip1 = "用户名格式错误";
      } else {
        this.tip1 = "";
      }
    },
    numberCheck(e) {
      e.currentTarget.nextElementSibling.style.display="block";
      var filter = /^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/; //正则
      if (this.usernumber == "") {
        this.tip3 = '手机号码不能为空';
      } else if (!filter.test(this.usernumber)) {
        this.tip3 = "手机号码格式错误";
      } else {
        this.tip3 = "";
      }
    },
    goRegister() {
      if (this.username == "" || this.password == ""||this.usernumber == "") {
        alert("请输入信息");
      }
      else if(this.tip1 == "" && this.tip2 == ""&&this.tip3==""){
        let self = this;
        let data = {
          regName: this.username,
          regPasswd: this.password,
          regNumber: this.usernumber,
        };
        self.$http.post("/reg", data).then(
          res => {
            console.log(res);
            if (res.status == 200) {
              if (res.data.status == 1) {
                this.$store.userName = this.username;
                alert('注册成功');
                self.$router.push("/login");
              } else {
                alert(res.data.msg);
              }
            } else {
              alert("请求失败");
            }
          },
          error => {
            console.log(error);
          }
        );
      }
      else {
        alert('信息错误')
      }
    }
  },
  mounted() {
    this.$store.dispatch("hideNav"); //触发store中的隐藏的方法
  },
  destroyed() {
    this.$store.dispatch("showNav"); //触发store中的显示的方法
  }
};
</script>
<style lang="stylus" scoped>
body {
  background-color: #F3F5F7;
}

.user_login_box {
  max-width: 640px;
  min-width: 300px;
  margin: 0 auto;
  height: 200px;
  padding-top: 40px;

  .login_dialog {
    width: 100%;
    height: 100%;
    padding: 10px;

    .tip-error {
      height: 20px;
      color: red;
    }

    .input {
      height: 50px;
      width: 100%;
      background-color: #fff;
      margin-top: 20px;
      border: 1px solid #bebebe;
      border-radius: 2px;

      .user_input {
        width: 100%;
        height: 100%;
        font-size: 16px;
        padding-left: 6px;
      }
    }

    .u_passwd, .u_number {
      margin-top: 20px;
    }

    .login_box {
      height: 50px;
      width: 100%;
      background-color: #fff;
      border: 1px solid #F23030;
      border-radius: 4px;
      margin-top: 30px;

      a {
        display: block;
        width: 100%;
        height: 100%;
        line-height: 50px;
        text-align: center;
        color: #F23030;
      }
    }
  }
}
</style>



