<template>
  <div>
    <div class="top_bar">
      <a @click="indexChange" class="icon_back"></a>
      <h3 class="cartname">我的订单</h3>
    </div>

    <div class="my_order_wrap">
      <div class="my_order_inner">
        <div class="my_order">
          <div class="order_box" v-for="(item,index) in Datas" :key="">
            <div class="order_head">
              <div class="icon_shop"></div>
              <div class="shop_title">
                <span class="title">{{item.shop_name}}</span>
              </div>
            </div>
            <div class="order_box_hd_del"></div>
              <div class="order_item">
                <div class="oi_content">
                  <div class="cover">
                    <div class="wqvue-image img">
                      <img v-lazy="item.product_img_url">
                    </div>
                  </div>
                  <div class="content">
                    <router-link :to="'/detail/'+item.product_id">
                      <div class="desc">{{item.product_name}}</div>
                    </router-link>
                  </div>
                </div>
                <div class="order_total_bar">
                  <div class="total_count">共{{item.num}}件商品</div>
                  <div class="payment">实付金额：<span class="price">¥{{item.total}}</span></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters, mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      Datas: [],
      arrPro: [],
      total: [],
      flag: true
    };
  },
  computed: {
    ...mapState(["id"])
  },
  methods: {
    indexChange() {
      this.$store.commit("CHANGENBINDEX", 4);
      this.$router.push("/mine");
    },
    init() {
      let self = this;
      self.$http.get("/order1", { params: { mId: self.id } }).then(
        res => {
          if (res.status == "200") {
            self.arrPro = res.data;
            self.arrPro.forEach(function(item, index) {
              let pid = item.product_id;
              self.$http.get("/cart2", { params: { Id: pid } }).then(
                res => {
                  if (res.status == "200") {
                    self.Datas[index] = res.data[0];
                    self.Datas[index].num = self.arrPro[index].ocount;
                    self.Datas[index].order_id = self.arrPro[index].order_id;
                    self.Datas[index].total =
                      self.Datas[index].num * self.Datas[index].product_uprice;
                    let sid = res.data[0].shop_id;
                    self.$http.get("/cart3", { params: { Id: sid } }).then(
                      res => {
                        if (res.status == "200") {
                          self.Datas[index].shop_name = res.data[0].shop_name;
                        }
                      },
                      error => {
                        console.log(error);
                      }
                    );
                  }
                },
                error => {
                  console.log(error);
                }
              );
            });
          }
        },
        error => {
          console.log(error);
        }
      );
    }
  },
  mounted() {
    this.$store.dispatch("hideNav"); //触发store中的隐藏的方法
    this.init();
  },
  destroyed() {
    this.$store.dispatch("showNav"); //触发store中的显示的方法
  }
};
</script>
<style lang="stylus" scoped>
.my_order_wrap {
  width: 100%;
  overflow: hidden;
  padding-top: 44px;

  .my_order_inner {
    width: 400%;
    -webkit-transform: translateX(0);
    transform: translateX(0);
    -webkit-transition: none;
    transition: none;
    overflow: hidden;

    .my_order {
      width: 25%;
      overflow: hidden;
      min-height: 200px;
      float: left;
      background-color: #f7f7f7;

      .order_box {
        margin: 15px;
        padding: 10px;
        background: #fff;
        border-radius: 8px;

        .order_head {
          font-family: PingFang-SC-Medium;
          line-height: 24px;
          position: relative;
          -webkit-box-align: center;
          -webkit-align-items: center;
          align-items: center;

          .icon_shop {
            display: inline-block;
            width: 20px;
            height: 20px;
            margin-right: 5px;
            -webkit-align-self: center;
            align-self: center;
            background: url('../../assets/images/shop.png') no-repeat center;
          }

          .shop_title {
            overflow: hidden;

            .title {
              font-size: 16px;
              color: #333;
              position: relative;
              max-width: 90%;
              padding-right: 12px;
              display: inline-block;
              vertical-align: middle;
            }
          }

          .order_state {
            font-size: 14px;
            color: #999;
            -webkit-flex-shrink: 0;
            flex-shrink: 0;
          }

          .order_box_hd_del {
            display: inline-block;
            width: 35px;
            height: 21px;
            position: relative;
            vertical-align: middle;
            -webkit-flex-shrink: 0;
            flex-shrink: 0;
          }

          .order_box_hd_del:before {
            content: '';
            position: absolute;
            width: 1px;
            height: 15px;
            background-color: #e3e3e3;
            top: 4px;
            left: 10px;
          }

          .order_box_hd_del:after {
            content: '';
            position: absolute;
            top: 4px;
            right: 0;
            width: 18px;
            height: 18px;
            background: url('../../assets/images/delete_down.png') no-repeat;
            background-size: 18px 18px;
            margin-top: -3px;
          }
        }

        .order_item {
          .oi_content {
            margin-top: 8px;
            overflow: hidden;

            .cover {
              width: 75px;
              height: 75px;
              margin-right: 10px;
              border-radius: 2px;
              -webkit-flex-shrink: 0;
              flex-shrink: 0;
              position: relative;

              .wqvue-image {
                display: inline-block;
                overflow: hidden;
                position: relative;
                width: 100%;
                height: 100%;
                border-radius: 2px;

                img {
                  width: 100%;
                  height: 100%;
                  border-radius: 2px;
                  position: absolute;
                  left: 50%;
                  top: 50%;
                  transform: translate3d(-50%, -50%, 0);
                }
              }
            }

            .content {
              color: #333;
              -webkit-align-self: center;
              align-self: center;

              .desc {
                font-family: PingFangSC-Regular;
                font-size: 14px;
                color: #333;
                line-height: 21px;
              }
            }
          }

          .order_total_bar {
            padding: 6px 0;
            position: relative;

            .total_count {
              -webkit-box-flex: 1;
              box-flex: 1;
              -webkit-flex: 1;
              flex: 1;
              font-size: 12px;
              color: #999;
              text-align: right;
              line-height: 21px;
            }

            .payment {
              margin-left: 10px;
              font-size: 14px;
              color: #999;
              line-height: 21px;

              .price {
                color: #151515;
              }
            }
          }
        }
      }
    }
  }
}
</style>



