<template>
  <div class="wrapper">
    <swiper :options="swiperOption" v-if="showSwiper" ref="mySwiper" >
      <swiper-slide v-for="(item,index) of imgDatas" :key="index">
        <img class="swiper-img" :src="item" />
      </swiper-slide>
      <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
export default {
  name: "HomeBanner",
  props: {
    imgDatas: Array
  },
  data() {
    return {
      swiperOption: {
        pagination: {
          el:'.swiper-pagination'
        },
        loop: true,
        autoplay: {
          delay:2000,
          disableOnInteraction:false
        }
      }
    };
  },
  computed: {
    showSwiper() {
      return this.imgDatas.length;
    }
  }
};
</script>

<style lang="stylus" scoped>
.wrapper >>> .swiper-pagination-bullet-active {
  background: #fff;
}

.wrapper {
  overflow: hidden;
  width: 100%;
  height: 0;
  padding-bottom: 49.5%;
  background: #eee;

  .swiper-img {
    width: 100%;
  }
}
</style>



