<template>
<div>
  <div class="top_bar" ref="a">
		    <a @click="indexChange" class="icon_back"></a>
		    <h3 class="cartname">地址管理</h3>
	</div>
	<div class="address_item clearfix" v-for="item in addressDatas" ref="b" >
          <span class="sname">{{item.sname}}</span><span class="uphone">{{item.user_phone}}</span>
          <div class="address">{{item.addressarea}}</div><div class="edit" @click="adEdit(item.sname,item.user_phone,item.addressarea,item.address_id)"></div>
  </div>
  <div class="mod_btns fixed" ref="c">
    <a href="javascript:void(0);" class="mod_btn bg_1" @click="add">新增收货地址</a>
  </div>
  <div class="editPage" ref="d">
    <div class="item">
      <span class="tit">收货人</span><input type="text" id="name" v-model="name">
    </div>
    <div class="item">
      <span class="tit">联系方式</span><input type="text" id="phone" v-model="phone">
    </div>
    <div class="item addDetail">
      <span class="tit">详细地址</span><input type="text" id="address" v-model="address">
    </div>
    <div class="mod_btns"> <a href="javascript:void(0);" class="mod_btn bg_1" id="submitAddress" @click="confirm">确认</a> </div>
    <div class="mod_btns" v-show="type=='edit'"> <a href="javascript:void(0);" class="mod_btn bg_1 del" @click="del">删除收货地址</a> </div>
  </div>
</div>

</template>
<script>
import { mapGetters, mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      addressDatas: [],
      name: "",
      phone: "",
      address: "",
      adId: "",
      type: ""
    };
  },
  computed: {
    ...mapState(["id"])
  },
  methods: {
    indexChange() {
      this.$store.commit("CHANGENBINDEX", 4);
      this.$router.push("/mine");
    },
    getData(id) {
      let self = this;
      self.$http.get("/address", { params: { mId: id } }).then(
        res => {
          if (res.status == "200") {
            self.addressDatas = res.data;
            console.log(self.addressDatas);
          }
        },
        error => {
          console.log(error);
        }
      );
    },
    adEdit(name, phone, address, address_id) {
      this.change();
      this.name = name;
      this.phone = phone;
      this.address = address;
      this.adId = address_id;
      this.type = "edit";
    },
    confirm() {
      let self = this;
      let data = {
        mname: this.name,
        mphone: this.phone,
        maddress: this.address,
        mid: this.adId,
        uid: this.id
      };
      if (this.name == "" || this.phone == "" || this.address == "") {
        alert("信息不能为空!");
        return;
      }
      if (this.type == "edit") {
        self.$http.post("/addressEdit", data).then(
          res => {
            if (res.status == "200") {
              this.$router.go(0);
            }
          },
          error => {
            console.log(error);
          }
        );
      } else {
        self.$http.post("/addAddress", data).then(
          res => {
            if (res.status == "200") {
              this.$router.go(0);
            }
          },
          error => {
            console.log(error);
          }
        );
      }
    },
    add() {
      this.change();
      this.name = "";
      this.phone = "";
      this.address = "";
      this.type = "add";
    },
    del() {
      let self = this;
      self.$http.post("/delAdd", { params: { mId: this.adId } }).then(
        res => {
          if (res.status == "200") {
            this.$router.go(0);
          }
        },
        error => {
          console.log(error);
        }
      );
    },
    change() {
      this.$refs.a.style.display = "none";
      this.$refs.b.forEach(function(item, index) {
        item.style.display = "none";
      });
      this.$refs.c.style.display = "none";
      this.$refs.d.style.display = "block";
    }
  },
  mounted() {
    this.getData(this.id);
    this.$store.dispatch("hideNav"); //触发store中的隐藏的方法
  },
  destroyed() {
    this.$store.dispatch("showNav"); //触发store中的显示的方法
  }
};
</script>
<style lang="stylus" scoped>
@import '../../assets/css/account.styl';
</style>



