<template>
  <div>
    <div v-show="flag2">
      	<div id="carttp">
		<header class="top_bar">
      <a onclick="window.history.go(-1)" class="icon_back"></a>
      <h3 class="cartname">确认订单</h3>
		</header>
	  </div>
      <div class="address_defalut_wrap">
      <div class="address_item clearfix">
        <span class="sname">{{this.addressData.sname}}</span><span class="uphone">{{this.addressData.user_phone}}</span>
        <div class="address">{{this.addressData.addressarea}}</div><div class="edit" @click="selectAdd">></div>
      </div>
    </div>
    <div class="orderList" v-for="(item,index) in Datas">
      <div class="p">
        <div class="top">
          <div class="orderInfo">
            <div class="order_shopBar">
              <i class="order_shopBar_shop"></i>
              <span class="pop_vname">{{item.shop_name}}</span>
            </div>
          </div>
        </div>
        <ul>
          <li class="hproduct">
            <img :src="'http://'+item.product_img_url+'.jpg'" class="photo">
            <div class="fn"><strong>{{item.product_name}}</strong></div>
            <p class="sku_price">¥<span>{{item.product_uprice}}</span></p>
            <div class="sku">
              <div class="sku_num" style="">×{{item.num}}</div>
            </div>
            <ul class="order_info_tips">
              <li>
                <span>⑦</span> 支持7天无理由退货
              </li>
              <li>
                <span><img class="optional pricetip" src="../../assets/images/att.png"></span>
                价格说明
              </li>
            </ul>
          </li>
        </ul>
        <ul class="order_info_list">
          <li class="shipping" shiptype="shipSop" >
            <strong>配送服务</strong>
            <div class="shipping_content">
              <p>快递运输</p>
              <div class="type_justify">
                <div class="left">中小件送货时间</div>工作日、双休日与节假日均可送货</div>
              </div>
           </li>
        </ul>
        <div class="order_info_guarantee">
            <h5 class="order_info_guarantee_title">退换无忧</h5>
            <p class="order_info_guarantee_content">  7天内退货，15天内换货，可享一次运费赔付到小金库</p>
        </div>
        <div class="buy_msg_v2">
          <div class="buy_msg_v2_tit">店铺备注</div>
          <input type="text" placeholder="选填，给商家留言">
        </div>
      </div>
    </div>
    <div class="buy_section" id="feeDetail">
      <ul class="buy_chart">
        <li class="buy_chart_item">
          <p class="buy_chart_item_text">商品金额</p>
          <p class="buy_chart_item_price">¥&nbsp; {{this.$store.state.sum}}</p>
        </li>
        <li class="buy_chart_item">
          <p class="buy_chart_item_text">运费
            <small class="buy_chart_item_tip"></small>
          </p>
          <p class="buy_chart_item_price">
            +&nbsp;¥0.00
          </p>
        </li>
      </ul>
    </div>
    <div id="payArea" class="pay_area">
      <p class="price">总价：
        <strong id="pageTotalPrice" >¥{{this.$store.state.sum}}</strong>
      </p>
      <div class="mod_btns" id="btnPayOnLine" style="">
        <a href="javascript:void(0);" class="mod_btn bg_2" @click="paypal">在线支付</a>
      </div>
    </div>
    </div>
    <div class="top_bar" v-show="flag">
		    <a @click="closeAdd" class="icon_back"></a>
		    <h3 class="cartname">收货地址</h3>
	  </div>
    <div class="address_item2 clearfix" v-for="item in addressDatas" v-show="flag" @click="select(item)">
        <span class="sname">{{item.sname}}</span><span class="uphone">{{item.user_phone}}</span>
        <div class="address">{{item.addressarea}}</div>
    </div>
    <div class="pop" v-show="popStatus">
          <div class="pop_box">
              <div class="del_info">
                  确定购买吗？
              </div>
              <div class="del_cancel" @click="payCancel">
                  取消
              </div>
              <div class="del_ok" @click="payOk">
                  确定
              </div>
          </div>
    </div>
  </div>
</template>

<script>
//mapGetters 辅助函数  获取store中的getters值
import { mapGetters, mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      addressData: [],
      addressDatas: [],
      imgurl: "",
      Datas: [],
      flag: false,
      flag2: true,
      popStatus: false
    };
  },
  computed: {
    ...mapState(["id", "buyDatas"])
  },
  methods: {
    paypal: function() {
      this.popStatus = true;
    },
    payCancel: function() {
      this.popStatus = false;
    },
    payOk: function() {
      //确定
      this.popStatus = false;
      var el = this.Datas;
      var date = new Date();
      for (let index = 0; index < el.length; index++) {
        let data = {
          user_id: this.id,
          price: el[index].product_uprice,
          count: el[index].num,
          proid: el[index].product_id,
          tp: Number(el[index].product_uprice) * Number(el[index].num),
          adid: this.addressData.address_id,
          sta: 1,
          time:
            date.getFullYear() +
            "-" +
            (date.getMonth() + 1) +
            "-" +
            date.getDate() +
            " " +
            date.getHours() +
            ":" +
            date.getMinutes() +
            ":" +
            date.getSeconds()
        };
        if (el[index].order_id) {
          data.order_id = el[index].order_id;
          this.$http.post("/addOrder2", data).then(
            res => {
              if (res.status == "200") {
                this.$router.push("/home");
              }
            },
            error => {
              console.log(error);
            }
          );
        } else {
          this.$http.post("/addOrder", data).then(
            res => {
              if (res.status == "200") {
                this.$router.push("/home");
              }
            },
            error => {
              console.log(error);
            }
          );
        }
      }
    },
    getData(id) {
      let self = this;
      self.$http.get("/address", { params: { mId: id } }).then(
        res => {
          if (res.status == "200") {
            self.addressData = res.data[0];
            self.addressDatas = res.data;
          }
        },
        error => {
          console.log(error);
        }
      );
    },
    init() {
      this.getData(this.id);
      this.$store.dispatch("hideNav"); //触发store中的隐藏的方法
      for (let index = 0; index < this.buyDatas.length; index++) {
        let el = this.buyDatas[index].product_img_url;
        let idx1 = el.indexOf("//") + 2;
        let idx2 = el.indexOf(".jpg");
        let arg = el.substring(idx1, idx2);
        this.buyDatas[index].product_img_url = arg;
      }
      this.Datas = this.buyDatas;
      this.closeAdd();
    },
    closeAdd() {
      this.flag = false;
      this.flag2 = true;
    },
    selectAdd() {
      this.flag = true;
      this.flag2 = false;
    },
    select(item) {
      this.closeAdd();
      this.addressData = item;
    }
  },
  mounted() {
    this.init();
  },
  destroyed() {
    this.$store.dispatch("showNav"); //触发store中的显示的方法
  }
};
</script>
<style lang="stylus" scoped>
