  .address_item {
    max-width: 640px;
    min-width: 300px;
    margin: 0 auto;
    padding-left: 20px;
    padding-bottom: 10px;
    background: white;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    padding-top: 10px;

    .sname {
      font-weight: bold;
      margin-right: 10px;
    }

    .address {
      float: left;
      width: 300px;
      text-align: left;
      margin-top: 5px;
      color: #666;
    }

    .uphone {
      font-weight: bold;
    }

    .edit {
      width: 20px;
      z-index: 300;
      height: 20px;
      float: right;
      margin-top: 4px;
      color: #888;
      font-size: 20px;
      line-height: 20px;
    }
  }
}

.orderList {
  background: #fff;
  margin-top: 15px;
  margin-bottom: 15px;

  .p {
    padding: 0 10px;

    .top {
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);
      height: 40px;

      .orderInfo {
        position: relative;

        .order_shopBar {
          display: block;
          position: relative;
          height: 40px;
