            background-size: 16px auto;
            background: url('../../assets/images/shop.png') no-repeat center;
          }
        }
      }
    }

    .hproduct {
      position: relative;
      min-height: 90px;
      padding: 0;
      font-size: 12px;
      padding-top: 15px;

      .photo {
        width: 75px;
        height: 75px;
        position: absolute;
        top: 15px;
        left: 0;
      }

      .fn {
        color: #333;
        line-height: 20px;
        margin-bottom: 5px;
        font-size: 14px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
      }

      .sku_price {
        padding-left: 85px;
        height: 30px;
        line-height: 30px;
        color: #e93b3d;
        font-size: 10px;

        span {
          font-size: 16px;
        }
      }

      .sku {
        height: 30px;
        margin-top: -30px;
        text-align: right;
        word-wrap: break-word;
        word-break: break-all;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

        .sku_num {
          line-height: 30px;
