      top: 0;
      right: -10px;
    }

    .buy_msg_v2 {
      position: relative;
      width: 100%;
      height: 45px;
      background-color: #fff;
      box-sizing: border-box;

      input {
        display: block;
        padding-left: 75px;
        width: 100%;
        font-size: 16px;
        height: 45px;
        line-height: 45px;
        box-sizing: border-box;
      }

      .buy_msg_v2_tit {
        position: absolute;
        top: 0;
        height: 45px;
        line-height: 45px;
        color: #333;
        font-size: 16px;
      }
    }

    .buy_msg_v2:before {
      content: '';
      position: absolute;
      z-index: 1;
      pointer-events: none;
      background-color: #e5e5e5;
