<template>
  <div>
    <h1>{{ msg }}{{$store.state.count}}</h1>
    <h1>{{count}}</h1>
  </div>
</template>

<script>
import { mapGetters} from 'vuex'
export default {
  data () {
    return {
      msg: 'find'
    }
  },
  computed :mapGetters(['count'])
}
</script>



