<template>
    <div class="goods_detail">
        <header class="top_bar">
            <a @click="goBack" class="icon_back"></a>
            <h3 class="cartname">商品详情</h3>
            <a href="#" class="icon_menu" @click="goHome()"></a>
        </header>
        <main class="detail_box">
            <section class="banner_box">
                <Banner :imgDatas ='goodsImages' v-if="flag"/>
            </section>
            <section class="product_info clearfix">
                <div class="product_left">
                    <p class="p_name">{{goodsData[0] ? goodsData[0].product_name : ''}}</p>
                    <div class="product_pric">
                        <span>￥</span>
                        <span class="rel_price">{{goodsData[0] ? goodsData[0].product_uprice : ''}}</span>
                        <span>.00</span>
                    </div>
                    <div class="product_right">
                        降价通知
                    </div>
                </div>

            </section>
            <section class="product_intro">
                <p class="pro_det">
                    {{goodsData[0] ? goodsData[0].product_detail : ''}}
                </p>
           </section>

        </main>
        <footer class="cart_d_footer">
            <div class="m">
                <ul class="m_box">
                    <li class="m_item">
                        <a href="javascript:void(0)" class="m_item_link">
                            <em class="m_item_pic"></em>
                            <span class="m_item_name">卖家</span>
                        </a>
                        <a href="javascript:void(0)" class="m_item_link">
                            <em class="m_item_pic two"></em>
                            <span class="m_item_name">关注</span>
                        </a>
                        <a href="javascript:void(0)" class="m_item_link">
                            <em class="m_item_pic three"></em>
                            <span class="m_item_name" @click="goCart">购物车</span>
                        </a>
                    </li>
                </ul>
                <div class="btn_box clearfix" >
                    <a href="javascript:void(0)" class="buy_now" @click="addToCart">加入购物车</a>
                    <a href="javascript:void(0)" class="buybuy"  @click="toBuy">立即购买</a>
                </div>
            </div>
        </footer>
    </div>
</template>
<script>
import { mapGetters, mapActions, mapState } from "vuex";
import Banner from "../public/Banner";
export default {
  data() {
    return {
      num: 0,
      goodsImages: [],
      goodsData: [],
      flag: false
    };
  },
  mounted() {
    this.getData(this.$route.params.id);
    this.$store.dispatch("hideNav"); //触发store中的隐藏的方法
  },
  computed: {
    ...mapState(["nbIndex", "id","userName"])
  },
  destroyed() {
    this.$store.dispatch("showNav"); //触发store中的显示的方法
  },
  methods: {
    goHome() {
      this.$store.commit("CHANGENBINDEX", 0);
      this.$router.push("/home");
    },
    goBack() {
      window.history.go(-1);
    },
    goCart() {
      this.$router.push("/cart");
    },
    getData(id) {
      let self = this;
      self.$http.get("/detail", { params: { mId: id } }).then(
        res => {
          if (res.status == "200") {
            self.goodsImages = res.data[0]; //轮播数据
            self.flag = true;
            self.goodsData = res.data[1]; //详情信息
            self.goodsData[0].num = 1;
          }
        },
        error => {
          console.log(error);
        }
      );
    },
    addToCart() {
      let data = JSON.parse(JSON.stringify(this.goodsData[0]));
      data.num = 1; //数量
      data.check = false; //商品的状态
      data.user_id = this.id;
      let self = this;
      if (!this.userName) {
        alert("请先登录!");
        return;
      }
      self.$http.post("/addCart", data).then(
        res => {
          if (res.status == "200") {
          }
        },
        error => {
          console.log(error);
        }
      );
    },

    toBuy() {
      console.log(this.goodsData);
      if (!this.userName) {
        alert("请先登录!");
        return;
      }
      this.$store.commit("SUM", this.goodsData[0].product_uprice);
      this.$store.commit("BUY", this.goodsData);
      this.$router.push("/pay");
      this.$router.go(0);
    }
  },
  components: {
    Banner
  }
};
</script>
<style>
@import "../../assets/css/detail.css";
</style>



