          text: "线上超市",
          img: require("../../assets/images/nav0.png")
        },
        {
          id: 1,
          text: "全球购",
          img: require("../../assets/images/nav1.png")
        },
        {
          id: 2,
          text: "服装城",
          img: require("../../assets/images/nav2.png")
        },
        {
          id: 3,
