          img: require("../../assets/images/nav8.png")
        },
        {
          id: 9,
          text: "我的关注",
          img: require("../../assets/images/nav9.png")
        }
      ]
    };
  }
};
</script>

<style lang="stylus" scoped>
.m_nav {
  max-width: 100%;
  height: 150px;

  ul {
    width: 100%;
    padding-top: 16px;
  }

  .nav_item {
    width: 20%;
    height: 69px;
    float: left;
    text-align: center;

    .nav_item_link {
      width: 100%;
      height: 100%;
      display: block;
      text-align: center;

      img {
        display: block;
        width: 30px;
        height: 30px;
        margin: 0 auto;
      }

      span {
        font-size: 12px;
        color: #666;
      }
    }
  }
}
</style>




