<template>
  <div class="footer">
  	<footer class="fixBottomBox">
        <ul>
            <router-link tag="li" v-for="(item,index) in list" :to="{path: '/' + item.to}" class="tabItem" :key="item.id" >
            	<a href="javascript:;" class="tab-item-link">
                    <img :src="item.img" alt="" class="tab-item-icon" @click="indexChange(item.id)">
                </a>
            </router-link>
        </ul>
    </footer>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      list: [
        {
          to: "home",
          id: 0,
          url: "../assets/images/a-home-2.png",
          img: require("../assets/images/a-home-2.png")
        },
        {
          to: "catgory",
          id: 1,
          url: "../assets/images/n-catergry.png",
          img: require("../assets/images/n-catergry.png")
        },
        {
          to: "search",
          id: 2,
          url: "../assets/images/n-find.png",
          img: require("../assets/images/n-find.png")
        },
        {
          to: "cart",
          id: 3,
          url: "../assets/images/n-cart.png",
          img: require("../assets/images/n-cart.png")
        },
        {
          to: "login",
          id: 4,
          url: "../assets/images/n-me.png",
          img: require("../assets/images/n-me.png")
        }
      ]
    };
  },
  computed: {
    ...mapState(["nbIndex"])
  },
  methods: {
    indexChange(i){
      this.$store.commit("CHANGENBINDEX", i);
      var i=this.nbIndex;
      this.list.forEach((item, index) => {
        var arg = item.url;
        let idx2 = arg.indexOf("-2");
        let idx = arg.indexOf("/") + 1;
        if (idx2 > -1 && index != i && i != 3) {
          arg = arg.substring(idx, idx2);
          item.url = "../" + arg + ".png";
          item.img = require("../" + arg + ".png");
        }
        if (index == i && idx2 == -1 && index != 3) {
          arg =
            this.list[i].url.substring(
              idx,
              this.list[i].url.indexOf("png") - 1
            ) + "-2";
          item.url = "../" + arg + ".png";
          item.img = require("../" + arg + ".png");
        }
      });
    }
  },
  watch: {
    nbIndex(){
      this.indexChange(this.nbIndex);
    }
  },
  mounted () {
    this.indexChange(this.nbIndex);
  }
};
</script>

<style lang="stylus" scoped>
.fixBottomBox {
  box-shadow: 0 -2px 10px #ccc;
  width: 100%;
  height: 50px;
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: #fff;

  ul {
    display: flex;
    justify-content: space-around;
  }

  .tabItem {
    width: 18%;
    height: 100%;
    text-align: center;

    .tab-item-link {
      display: inline-flex;
      width: 70px;
      height: 50px;

      .tab-item-icon {
        display: inline-flex;
        width: 100%;
        height: auto;
      }
    }
  }
}
</style>





